.Burger{
    width:100%;
    height:250px;
    margin:auto;
    overflow: scroll;
    font-weight: bold;
    font-size:1.2rem;
    text-align: center;

}

@media(max-width:500px) and (max-height: 400px){
    .Burger{
        width:350px;
        height:300px;
    }
}
@media(min-width:500px) and (min-height: 401px){
    .Burger{
        width:450px;
        height:400px;
    }
}
@media(min-width:1000px) and (min-height: 700px){
    .Burger{
        width:700px;
        height:600px;
    }
}